.hamburger {
	display: none;
}

.header-1{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 20px;
}

.nav a{
	text-decoration: none;
	padding: 10px;
	color: white;
}

.nav button{
	padding: 9px 20px;
	border-radius:30px;
	border: none;
	background: white;
	text-transform: uppercase;
}
.nav button:hover{
	background:inherit ;
	color: white;
}
.sunny-text{
	text-align: center;
	letter-spacing: 2px;
	text-transform: uppercase;
	margin-top: 15px;
	padding-bottom: 200px;
	color: white;
}

.bigger-header{
	background-image: url(../images/desktop/image-header.jpg);
	background-size: 100% 100%;
	position: relative;

}

.org-name{
	color: white;
	font-size: 20px;
}
.arrow{
	position: absolute;
	top: 43%;
	right: 50%;
}
.hamburger-nav{
	width: 90%;
	background: white;
	text-align: center;
	display: none;
	margin: 20px;
}
.hamburger-nav a{
	text-decoration: none;
	display: block;
	padding: 20px;
	color:hsl(212, 27%, 19%) ;
}
.hamburger-nav button{
	padding: 9px 20px;
	border-radius:30px;
	border: none;
	background: yellow;
	text-transform: uppercase;
	color: black;
	margin-bottom: 20px;
}

@media only screen and (max-width: 375px){
	.hamburger{
		display:block;
	}
	.nav{
		display: none;
	}
	.bigger-header{background-image: url(../images/mobile/image-header.jpg);}
	.arrow{top: 55%;}
}