*{
	box-sizing: border-box;
}

.row1{
	display: flex;
	flex-direction: row-reverse;
	align-items: center;
}
.row{
	display: flex;
	align-items: center;
	width: 100%;
}
.text{
	padding: 40px;
	width: 50%;

}

.first{
}

.image{
	width: 50%;
	height: 300px;
}

.image-desktop{width: 100%;height:100%;}
.embed{
	bottom: 0%;
	text-align: center;
	padding: 30px;
	padding-bottom: 0px;
}

.image-mobile{
	display: none;
}

.profile{
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 30px;
}
.profile-all{text-align: center;padding: 40px;}
.single-profile img{
	border-radius: 50%;
	width: 20%;
}
.single-profile{
	text-align: center;
}

.dv{
	display: flex;
}
.mv{
	display: none;
}
.FGdesktop{
	width: 25%;
}
@media only screen and (max-width: 375px){
	.image-mobile{
		display: block;
		width: 100%;
		height: 300px;
	}
	.image-desktop{
		display: none;
	}
	.dv{
		display: none;
	}
	.mv{
		display: grid;
		grid-template-columns: auto auto;
	}
	.FGdesktop{
		width: 100%;
	}
	.row{
		display: block;
	}
	.profile{
		display: block;
	}
	.text{
		width: 100%;
	}
	.image{
		width: 100%;

	}
}